import React, { useState } from 'react';
import { ethers } from 'ethers';
import Web3Modal from 'web3modal';
import './App.css';

const at3SwapAddress = "0x03d3C84F68306B9b3eD66f393bf45E16198641f0"; // Reemplaza con la direccin del contrato AT3Swap desplegado
const usdtAddress = "0xc2132D05D31c914a87C6611C10748AEb04B58e8F";  // Reemplaza con la direccin del contrato USDT
const usdcAddress = "0x3c499c542cEF5E3811e1192ce70d8cC03d5c3359";  // Reemplaza con la direccin del contrato USDC

function App() {
    const [provider, setProvider] = useState(null);
    const [account, setAccount] = useState(null);
    const [amount, setAmount] = useState(0);
    const [at3Amount, setAt3Amount] = useState(0);
    const tokenPrice = 1.05; // Precio fijo de 1.05 USDT/USDC por 1 AT3

    const connectWallet = async () => {
        const web3Modal = new Web3Modal();
        const instance = await web3Modal.connect();
        const provider = new ethers.providers.Web3Provider(instance);
        setProvider(provider);

        const signer = provider.getSigner();
        const address = await signer.getAddress();
        setAccount(address);
    };

    const calculateAT3 = (inputAmount) => {
        // Calcula la cantidad de AT3 basada en el precio de 1.05 USDT por AT3
        const calculated = inputAmount / tokenPrice;
        setAt3Amount(calculated);
    };

    const approveTokens = async (amount, stableCoinAddress) => {
        if (!provider) return;
        const signer = provider.getSigner();

        // Convertir la cantidad de USDT/USDC a la menor unidad (6 decimales)
        const amountInWei = ethers.utils.parseUnits(amount.toString(), 6);

        const stableCoinContract = new ethers.Contract(stableCoinAddress, [
            "function approve(address spender, uint256 amount) public returns (bool)"
        ], signer);

        const approveTx = await stableCoinContract.approve(at3SwapAddress, amountInWei);
        await approveTx.wait();

        console.log(`Approved ${amount} USDT/USDC for ${at3SwapAddress}`);
    };

    const swapForAT3 = async (amount, stableCoinAddress) => {
        if (!provider) return;

        try {
            // Asegrate de que los tokens estn aprobados primero
            await approveTokens(amount, stableCoinAddress);

            const signer = provider.getSigner();
            const at3SwapContract = new ethers.Contract(at3SwapAddress, [
                "function swapForAT3(uint256 amount, address stableCoinAddress) public"
            ], signer);

            // Convertir la cantidad de USDT/USDC a 6 decimales
            const amountInWei = ethers.utils.parseUnits(amount.toString(), 6);

            // Realizar el intercambio
            const tx = await at3SwapContract.swapForAT3(
                amountInWei,
                stableCoinAddress,
                { gasLimit: ethers.utils.hexlify(500000) }  // Lmite de gas ajustado segn sea necesario
            );
            await tx.wait();

            console.log(`Swapped ${amount} USDT/USDC for AT3`);
        } catch (error) {
            console.error("Error in swapForAT3:", error);
            if (error.code === "UNPREDICTABLE_GAS_LIMIT") {
                console.error("Gas estimation failed. The transaction might fail or require manual gas limit.");
            } else {
                console.error("Transaction failed:", error);
            }
        }
    };

    return (
        <div className="App">
            <button onClick={connectWallet}>Conectar MetaMask</button>
            {account && <p>Cuenta conectada: {account}</p>}
            <input 
                type="number" 
                placeholder="Cantidad de USDT/USDC" 
                onChange={(e) => {
                    setAmount(e.target.value);
                    calculateAT3(e.target.value);
                }}
            />
            <p>Recibirs {at3Amount} AT3</p>
            <button onClick={() => swapForAT3(amount, usdtAddress)}>Intercambiar AT3 con USDT</button>
            <button onClick={() => swapForAT3(amount, usdcAddress)}>Intercambiar AT3 con USDC</button>
        </div>
    );
}

export default App;
